//////
///// @at-root #&__zk
/////

@include font(SeatBcn-Web-Regular, SeatBcn-Web-Regular);
@include font(SeatBcn-Web-Medium, SeatBcn-Web-Medium);
@include font(SeatBcn-Web-Bold, SeatBcn-Web-Bold);
@include font(SeatBcn-Web-Black, SeatBcn-Web-Black);

// :root
//     --transition-1: all 0.3s ease-in-out
//     --transition-2: all 0.1s ease-in-out
* {
	&:focus {
		outline: 0; } }

html {
    * {
        transition: all 0.3s ease-in-out;
        font-size: 100%; } }

body {
	font-family: SeatBcn-Web-Regular, sans-serif;
	@include fs(16); }

h2 {
	padding: 30px 50px 0 50px;
	font-family: SeatBcn-Web-Black, sans-serif;
	font-weight: 900;
	text-align: center;
	@include fs(16);
	@include md {
		padding: 50px 50px 0 50px;
		@include fs(24); }
	@include lg {
		@include fs(30); } }

section {
	&:after {
		content: " ";
		display: block;
		clear: both; } }

.header-line {
	transition: none;
	&.is-fixed-xl {
		transition: none;
		@include xl {
			transition: none;
			position: fixed;
			z-index: 3;
			left: 50%;
			transform: translateX(-50%); } } }

.header {
	display: flex;
	flex-direction: column;
	@include xl {
		flex-direction: row; }
	.mob-logo {
		background: #ffffff;
		z-index: 3;
		display: block;
		padding: 9px 0px 9px 16px;
		height: 100%;
		border-bottom: 1px solid #d3d3d3;
		svg {
			width: 82px;
			@include md {
				width: 102px; } }
		@include xl {
			display: none; } }

	&__logo {
		background: #ffffff;
		padding: 9px 0px 9px 16px;
		display: block;
		height: 100%;
		border-right: 1px solid #C2C2C2;
		padding: 0 50px;
		svg {
			padding-top: 18px;
			width: 140px; } }

	&__menu-button {
		width: 22px;
		position: absolute;
		top: 12px;
		right: 16px;
		cursor: pointer;
		z-index: 4;
		@include md {
			top: 13px;
			right: 26px; }
		@include xl {
			display: none; }
		span {
			display: block;
			height: 2px;
			background-color: #000000;
			border-radius: 1px;
			margin-bottom: 5px;
			&:nth-child(1) {
				width: 17px; }
			&:nth-child(2) {
				width: 22px; }
			&:nth-child(3) {
				width: 15px; } }
		&.is-clicked {
			span {
				&:nth-child(1) {
					width: 100%;
					transform: rotate(45deg) translateY(5px) translateX(5px); }
				&:nth-child(2) {
					opacity: 0;
					width: 0; }
				&:nth-child(3) {
					width: 100%;
					transform: rotate(-45deg) translateY(-5px) translateX(5px); } } } }
	&__menu {
		padding-top: 40px;
		font-family: SeatBcn-Web-Medium, sans-serif;
		position: absolute;
		z-index: 2;
		width: 100%;
		background: #ffffff;
		top: -100%;
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		@include fs(14);
		@include md {
			padding-top: 56px;
			& > li {
 } }				//padding-left: 27px
		@include xl {
			top: 0;
			position: relative;
			padding-top: 0;
			flex-direction: row;
			justify-content: space-evenly;
			& > li {
				padding-left: 0px;
				border: none;
				div {
					line-height: 70px;
					&:not(.header__menu--is-gray):after {
						top: 9px;
						right: -20px; } } } }
		& > li {
			padding-left: 0;
			border-bottom: 1px solid #D3D3D3;
			cursor: pointer;
			&.menu-logo {
				display: none;
				@include xl {
					display: block; } }
			&.is-opened {
				.sub-menu {
					height: auto;
					visibility: visible;
					border-top: 1px solid #c2c2c2; }
				div {
					&:before {
						@include xl {
							pointer-events: none;
							content: "";
							position: absolute;
							z-index: 2;
							left: 50%;
							bottom: -4px;
							width: 8px;
							height: 8px;
							background-color: #fff;
							border-top: 1px solid #ccc;
							border-left: 1px solid #ccc;
							transform: rotate(45deg); } } }
				div:not(.header__menu--is-gray):after {
					transform: rotate(360deg); } }
			div {
				position: relative;
				line-height: 58px;
				margin-left: 25px;
				&:not(.header__menu--is-gray):after {
					content: "";
					position: absolute;
					right: 22px;
					top: 7px;
					bottom: 7px;
					width: 10px;
					background-image: url(../img/arrow.svg);
					background-repeat: no-repeat;
					background-size: 10px 7px;
					background-position: 100%;
					transform: rotate(180deg);
					@include md {
						right: 32px; } }
				a {
					color: #000000;
					font-family: SeatBcn-Web-Medium, sans-serif;
					text-decoration: none;
					@include fs(14); }
				&.header__menu--is-gray {
					a {
						font-family: SeatBcn-Web-Regular, sans-serif;
						color: #636363; } } } } }
	.sub-menu {
		transition: none;
		height: 0px;
		overflow: hidden;
		visibility: hidden;
		padding: 0;
		@include xl {
			display: flex;
			flex-wrap: wrap;
			padding: 0 10px;
			position: absolute;
			left: 0;
			width: 100%;
			background: #fff; }
		&.full-width {
			flex-wrap: nowrap; }
		a {
			display: block;
			line-height: 40px;
			color: #636363;
			text-decoration: none;
			font-weight: 500;
			@include fs(14);
			@include xl {
				color: #000000;
				font-family: SeatBcn-Web-Bold, sans-serif;
				span {
					display: inline; } }
			span {
				display: none; } }
		p {
			line-height: 14px;
			font-family: SeatBcn-Web-Regular, sans-serif;
			display: none;
			color: hsla(0,0%,6%,.6);
			@include xl {
				display: block; } }
		li {
			padding: 0;
			width: 100%;
			text-align: left;
			margin: 0 25px;
			@include xl {
				text-align: center;
				padding: 20px;
				margin: 5px 0 20px 0;
				width: 25%;
				float: left;
				&:hover {
					background: #fafaf9; } } }
		img {
			display: none;
			@include xl {
				display: block;
				width: 100%; } } } }

.banner {
	position: relative;
	&-line {
		height: 100%;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%); }
	&__image {
		width: 100%;
		&--mob-hide {
			display: none; }
		&--mob-show {
			display: block; }
		@include xl {
			&--mob-hide {
				display: block; }
			&--mob-show {
				display: none; } } }
	&__text {
		position: absolute;
		color: #ffffff;
		font-family: SeatBcn-Web-Black, sans-serif;
		font-weight: 900;
		left: 5%;
		bottom: 20%;
		line-height: 26px;
		@include fs(24);
		@include md {
			line-height: 42px;
			@include fs(40); }
		@include xl {
			top: 50%;
			left: 70%;
			transform: translateY(-50%);
			line-height: 54px;
			@include fs(50); } }
	@include xl {
 } }		//margin-bottom: 50px

.header-line,
.carsSelector-line,
.orderForm-line,
.banner-line,
.footerLinks,
.footerFooter {
	width: 100%;
	max-width: 100%;
	margin: 0 auto;
	@include xl {
		max-width: 1440px; } }

.carsCatalog {
	padding: 24px 12px;
	li {
		float: left;
		width: 100%;
		padding: 24px;
		&:hover {
			background-color: #F3F3F0;
			cursor: pointer; }
		&.is-selected {
			background-color: #F3F3F0;
			.is-checked {
				background: #32302E;
				border: 2px solid #ffffff; } }
		@include md {
			width: 50%; }
		@include lg {
			width: 33.33%; }
		@include xl {
			width: 25%; } }
	figure {
		margin: 0 auto; }
	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		margin: 0 auto;
		display: block; }
	&__image {
		position: relative;
		overflow: hidden;
		height: 0;
		padding-bottom: 45.333333333333%; }
	&__selector {
		font-family: SeatBcn-Web-Black, sans-serif;
		font-weight: 900;
		line-height: 24px;
		margin-top: 24px;
		@include fs(16); } }

.check {
	margin-right: 8px;
	float: left;
	width: 24px;
	height: 24px;
	border: 2px solid #32302E;
	border-radius: 50%;
	.is-checked {
		background: #ffffff;
		width: 100%;
		height: 100%;
		border: 10px solid #ffffff;
		border-radius: 50%; } }

.orderForm {
	margin: 80px auto 80px;
	padding: 0 16px;
	width: 100%;
	@include fs(16);
	@include md {
		padding: 0;
		width: 450px; }
	&__description {
		color: #636363;
		line-height: 26px;
		margin: 45px 0 70px 0; }
	&__button {
		position: relative;
		transform: translateX(-50%);
		left: 50%;
		display: inline-block;
		font-family: SeatBcn-Web-Bold, sans-serif;
		font-weight: 700;
		background: #EA5D19;
		opacity: 0.5;
		border-radius: 4px;
		color: #ffffff;
		//width: 100%
		padding: 20px 67px;
		text-align: center;
		@include fs(14);
		&:hover {
			cursor: not-allowed; }
		@include md {
			width: auto;
			padding: 17px 124px; }
		&.lets-go {
			opacity: 1;
			&:hover {
				cursor: pointer; } } }
	fieldset {
		margin-top: 50px;
		position: relative;
		border: none;
		&.empty {
			.after {
				background: linear-gradient(to right, red 100%, transparent 50%); }
			.error {
				display: block; } } }
	label {
		font-family: SeatBcn-Web-Medium, sans-serif!important;
		font-weight: 500;
		position: absolute;
		width: 100%;
		left: 0;
		top: 16px;
		color: #636363;
		transition: all 0.3s ease;
		&:hover {
			cursor: text; }
		span {
			transition: none;
			transform: rotate(180deg);
			float: right;
			width: 10px;
			margin-right: 10px;
			margin-top: 5px; } }
	input {
		width: 100%;
		border: none;
		margin-top: 15px;
		margin-bottom: 10px;
		@include fs(16);
		&:focus {
			outline: none;
			& ~ label {
				color: #636363!important;
				top: -10px;
				@include fs(12); }
			& ~ .after {
				background-position: 0 0; } }
		&:valid {
			& ~ label {
				color: #858584;
				top: -10px;
				@include fs(12);
				span {
					margin-top: 30px; } } } }
	input.input-dealer {
		&:focus {
			& ~ label {
				span {
					margin-top: 2px;
					transform: rotate(360deg); } }
			& ~ .dd-menu {
				visibility: visible; } } }
	.after {
		width: 100%;
		height: 1px;
		background: linear-gradient(to right, #858584 50%, transparent 50%);
		background-color: #D2D2D1;
		background-size: 200% 100%;
		background-position: 100% 0;
		transition: all 0.6s ease; }
	.error {
		position: absolute;
		top: 50px;
		left: 0;
		display: none;
		color: #EF808D;
		@include fs(14); }
	.error-auto {
		position: absolute;
		margin-top: 10px;
		left: 50%;
		transform: translateX(-50%);
		color: #EF808D;
		@include fs(14); } }

.dealer {
	* {
		transition: 0.01s!important; }
	position: relative; }
.dd-menu {
	visibility: hidden;
	width: 100%;
	position: absolute;
	top: 15px;
	padding: 13px 16px;
	background-color: #ffffff;
	border: 1px solid #D2D2D1;
	z-index: 2;
	li {
		z-index: 3;
		line-height: 48px;
		cursor: pointer;
		&:hover {
			color: #858584; } } }

// FOOTER
footer {
	background-color: #32302E;
	color: #ffffff;
	@include fs(14);
	a {
		font-family: SeatBcn-Web-Medium, sans-serif;
		color: #ffffff;
		text-decoration: none;
		line-height: 32px;
		&:hover {
			color: hsla(0,0%,100%,.6);
			svg {
				fill: hsla(0,0%,100%,.6); } } } }
.footerLinks {
	padding: 0;
	display: flex;
	justify-content: stretch;
	flex-direction: column;
	@include xl {
		padding: 0px 35px;
		flex-direction: row; }
	&__col {
		flex-grow: 1;
		padding: 0 16px 0 0;
		&--is-last {
			& .footerLinks__title {
				padding-left: 50px;
				@include xl {
					padding-left: 0; } }
			@include xl {
				order: 6; } }
		&--mob-hidden {
			display: none;
			@include xl {
				display: block; } } }
	&__title {
		margin: 0 0 0 16px;
		padding: 20px 0 20px 10px;
		color: #B6B5B4;
		font-family: SeatBcn-Web-Bold, sans-serif;
		border-bottom: 1px solid #979797;
		@include xl {
			border: none;
			margin: 40px 0 30px 0;
			padding: 0; } }
	ul {
		display: none;
		@include xl {
			display: block; } }
	svg {
		margin: 3px 0 0 3px;
		fill: #ffffff;
		width: 14px;
		transform: rotate(90deg); } }

.footerFooter {
	margin-top: 0;
	border: none;
	color: #B6B5B4;
	display: flex;
	flex-wrap: wrap;
	padding: 0 0 35px 0;
	@include xl {
		padding: 0 35px 52px 35px;
		border-top: 1px solid #979797;
		margin-top: 70px; }
	a {
		font-family: SeatBcn-Web-Regular, sans-serif;
		color: #B6B5B4;
		font-weight: 400;
		@include xl {
			color: #ffffff; } }
	&__logo {
		padding-top: 0px;
		order: 3;
		text-align: center;
		flex: 0 100%;
		@include xl {
			padding-top: 35px;
			flex: 0 50%;
			text-align: left;
			order: 1; } }
	&__social {
		padding: 30px 0;
		order: 2;
		flex: 0 100%;
		text-align: center;
		a {
			padding: 0 36px;
			@include xl {
				padding: 0 16xp; } }
		svg {
			zoom: 1.3;
			@include xl {
				zoom: 1; } }
		@include xl {
			flex: 0 50%;
			text-align: right;
			order: 2; } }
	&__copy {
		text-align: center;
		order: 4;
		flex-grow: 1;
		font-weight: 400;
		margin: 16px 0;
		@include fs(12);
		@include xl {
			text-align: left;
			order: 3; } }
	&__bottomLinks {
		order: 1;
		flex-grow: 1;
		ul {
			flex-direction: column;
			display: flex;
			margin: 0 16px;
			li {
				padding: 13px 0 13px 10px;
				border-bottom: 1px solid #979797; } }
		@include xl {
			flex-grow: 0;
			order: 4;
			ul {
				flex-direction: row;
				margin: 0;
				li {
					padding: 0 0 0 20px;
					border: none; } } } } }

// Pop-up

.is-hidden {
	opacity: 0;
	transition: all 0.2s ease-in-out;
	z-index: -10;
	visibility: hidden; }

.is-shown {
	opacity: 1;
	transition: all 0.2s ease-in-out;
	visibility: visible; }

.modal {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	height: 100%;
	margin: 0 auto;
	z-index: 4;
	text-align: center;
	&__shadow {
		width: 100%;
		height: 100%;
		position: fixed;
		display: block;
		background: #ffffff;
		opacity: 1;
		z-index: -1;
		cursor: pointer;
		@include md {
			background: transparent;
			//background: #161616
 } }			//opacity: 0.92
	&__body {
		overflow: scroll;
		background-color: #ffffff;
		position: relative;
		width: 100%;
		height: 100%;
		box-shadow: none;
		padding-top: 63px;
		@include md {
			padding-top: 50px;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			height: auto;
			max-width: 690px;
			border: 1px solid rgba(0, 0, 0, 0.12);
			box-shadow: 32px 32px 33px 0 rgba(0, 0, 0, 0.12); }
		h3 {
			font-family: SeatBcn-Web-Black, sans-serif;
			font-weight: 900;
			@include fs(16);
			@include md {
				@include fs(30); } }
		.button-close {
			width: 26px;
			height: 27px;
			position: absolute;
			top: 16px;
			right: 16px;
			&:hover {
				cursor: pointer; } }
		.action {
			background-color: #F3F3F0;
			padding: 26px 50px 56px 50px;
			margin-top: 32px;
			@include md {
				padding: 36px 120px 72px 120px;
				margin-top: 36px; }
			.agreement {
				color: #636363;
				line-height: 26px;
				padding-bottom: 24px;
				text-align: left;
				@include fs(12);
				@include md {
					padding-bottom: 30px;
					@include fs(16); } } }
		.buttons {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			@include md {
				flex-direction: row; } }
		.button {
			font-family: SeatBcn-Web-Bold, sans-serif;
			border-radius: 4px;
			padding: 17px 0;
			width: 100%;
			@include fs(14);
			&:hover {
				cursor: pointer; }
			@include md {
				width: 212px; } }
		.cancel {
			background: transparent;
			border: 2px solid #000000;
			margin-bottom: 24px;
			@include md {
				margin-bottom: 0; } }
		.submit {
			-webkit-appearance: none;
			color: #ffffff;
			background-color: #EA5D19;
			border: 1px solid #EA5D19;
			&:disabled {
				opacity: 0.5;
				cursor: progress; } } } }
.thankyou {
	display: none;
	padding: 0 120px 72px 120px;
	.manager {
		font-family: SeatBcn-Web-Bold, sans-serif;
		padding: 25px 0 100px 0;
		@include fs(14);
		@include md {
			padding: 40px 0 64px 0;
			@include fs(16); } }
	.home {
		color: #000000;
		text-decoration: none;
		margin: 0 auto;
		display: block;
		background: transparent;
		border: 2px solid #000000; } }

.confirmForm {
	margin-top: 24px;
	@include md {
		margin-top: 40px; }
	&> div:not(.action) {
		display: flex;
		flex-direction: column;
		padding: 0 65px 0 65px;
		justify-content: space-between;
		@include fs(14);
		@include md {
			flex-direction: row;
			padding: 0 120px 0 120px;
			@include fs(16); }
		label {
			text-align: left;
			line-height: 45px;
			width: 100%;
			@include md {
				width: 55%; } }
		input {
			font-family: SeatBcn-Web-Bold, sans-serif;
			width: 100%;
			@include md {
				width: 45%; } } } }
